.svg-react {
  display: block;
  margin: 0 auto;
  width: 50px;
}
@media (prefers-reduced-motion: no-preference) {
  .svg-react {
    animation: App-logo-spin infinite 5s linear;
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.footer {
  background: #000;
  padding: 20px;
}
