.container-fixed {
  height: 50vh;
  background: #000000;
}
.grid {
  display: flex;
}
.col {
  width: 50%;
}
.home-prefix {
  height: auto;
}
.images-home {
  width: 300px;
  margin: auto;
}
.impact-text {
  padding: 10px;
}
.impact-text h4 {
  width: 350px;
  margin: 10% auto;
  color: #fafafa;
  text-align: center;
  font-size: 34px;
  font-weight: bold;
}
.download-title {
  line-height: 1.38462;
  font-weight: 300;
  letter-spacing: 0.016em;
  font-family: Inter, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  margin-top: 60px;
  margin-bottom: 110px;
}
.download-title h2 {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 0px;
}
.download-title p {
  font-size: 24px;
  font-weight: 600;
}
.card {
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 20px;
  padding-bottom: 20px;
}
.data-app {
  margin: 30% auto;
}
.data-slick {
  margin: 20% auto;
}
.card-slider {
  background: #aa3535;
  margin: 30px;
  width: 200px !important;
  border-radius: 6px;
  box-shadow: 0 4px 20px rgba(149, 185, 193, 0.2);
}
.data-slider-content {
  height: 200px;
  width: 200px;
}
.images-slider {
  width: 200px;
}
.slick-next:before,
.slick-prev:before {
  font-size: 20px;
  line-height: 1;
  opacity: 0.75;
  color: #000000;
}
.home-app {
  background: #fafafa;
  height: 100vh;
}
.container-app {
  height: 100vh;
  background: #fafafa;
}
.container-react {
  height: 100vh;
  background: #000000;
}
.card-center {
  width: 50%;
  margin: 0 auto;
  padding-top: 30px;
}
.img-center {
  display: block;
  margin: 0 auto;
  width: 250px;
}

.title-build {
  text-align: center;
}
.title-build h3 {
  color: #fafafa;
  text-align: center;
  font-size: 30px;
  margin-bottom: 5px;
}
.title-build p {
  color: #ffffff;
  margin: 0px;
}
.card-title-slider {
  background: #ffffff;
  padding: 20px;
}
.card-title-slider h3 {
  margin: 0px;
  font-weight: "bold";
}
.download-btn {
  padding: 8px 12px;
  background: #000;
  color: #fafafa;
  text-align: center;
  border-radius: 2px;
  margin-top: 10px;
  cursor: pointer;
  font-size: 14px;
  font-weight: "bold";
}
.container-slick {
  margin-right: 20px;
}
.fakebar {
  height: 120px;
  width: 110%;
  top: -70px;
  position: relative;
  bottom: 0px;
  left: 0px;
  transform: skewY(-2deg) translate3d(-2px, 40px, 0px);
  backface-visibility: hidden;
  background: #000000;
  padding: 1px;
}
.slide {
  position: relative;
  overflow: hidden;
}
.hero-slider {
  width: 6060px;
  height: 270px;
  position: relative;
  z-index: 9;
  bottom: 0px;
  background: url("../../assets/images/hero.png") 0px 0px / contain repeat-x;
  animation: 70s linear 0s infinite normal none running slide;
}
@keyframes slide {
  0% {
    left: 0;
  }
  100% {
    left: -200%;
  }
}
.slick-dots li button::before {
  font-size: 10px;
}
.dribbble {
  background-image: url("../../assets/images/drible.png");
  width: 30px;
  height: 30px;
  margin: 10px;
  background-size: cover;
  margin: 0 auto;
}
.github {
  background-image: url("../../assets/images/github.png");
  width: 30px;
  height: 30px;
  margin: 10px;
  background-size: cover;
  margin: 0 auto;
}
.twitter {
  background-image: url("../../assets/images/twitter.png");
  width: 35px;
  height: 35px;
  margin: 10px;
  background-size: cover;
  margin: 0 auto;
}
.icon-social {
  flex-direction: row;
  display: flex;
}
.icon-social a {
  margin: 5% auto;
}
a {
  color: rgb(94, 228, 228);
  text-decoration: none;
}
