.header {
  padding: 20px;
  z-index: 99;
  flex: 1;
  height: 1.5em;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.black {
  background: #000000;
}
.white {
  background: #fafafa;
  backdrop-filter: saturate(180%) blur(20px);
  background-color: rgba(255, 255, 255, 0.72);
  -webkit-backdrop-filter: saturate(180%) blur(20px);
}
.header-content {
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.header-brand {
  padding-left: 10px;
}
.header-brand h3 {
  margin: 0;
  font-weight: 600;
}
.text-white {
  color: #ffffff;
}
.text-black {
  color: #000000;
}
